import React from 'react';
import { BpkAccordion, BpkAccordionItem, withSingleItemAccordionState } from 'bpk-component-accordion';
import BpkText from 'bpk-component-text';
import BpkInput from 'bpk-component-input';
import BpkLabel from 'bpk-component-label';
import BpkModal from 'bpk-component-modal';
import BpkButton from 'bpk-component-button';

import STYLES from './App.scss';

const c = className => STYLES[className] || 'UNKNOWN';

const SingleItemAccordion = withSingleItemAccordionState(BpkAccordion);


const NameForm = () => (
  <div>
    <div>
      <BpkLabel htmlFor="name">Name</BpkLabel>
      <BpkInput
        type="text"
        id="name"
        name="name"
        placeholder="What's your name?"
      />
    </div>
    <div>
      <BpkLabel htmlFor="age">Age</BpkLabel>
      <BpkInput
        type="number"
        id="age"
        name="age"
        placeholder="How old are you?"
      />
    </div>
  </div>
);

const isMobile = window.innerWidth < 700;

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      modalOpen: false,
    };
  }

  openModal = () => {
    this.setState({ modalOpen: true });
  }

  closeModal = () => {
    this.setState({ modalOpen: false });
  }

  render() {
    return (
      <div className={c('App')}>
        <main className={c('App__main')}id="pagewrap">
          <p>
            This is a simple proof of concept of a form that can display in either an accordion or modal depending on the viewport.
            When the window width is greater than 700px, you'll see an accordion. When it's less than that you'll get a button to open a modal.
          </p>
          <p>
            The code for the form is abstracted out so it can be shared between the desktop and mobile variants to avoid unnecessary duplication.
          </p>
          <p>
            The app does not reload when the width changes, you will need to manually refresh the page after changing the window width.
          </p>


          { isMobile ?
            <div>
              <BpkButton onClick={this.openModal}>Input passenger details</BpkButton>
              <BpkModal
                isOpen={this.state.modalOpen}
                onClose={this.closeModal}
                title="Passenger Details"
                getApplicationElement={() => document.getElementById('pagewrap')}
              >
                <NameForm />
              </BpkModal>
            </div>
        :
            <SingleItemAccordion>
              <BpkAccordionItem id="stops" title="Passenger Details">
                <NameForm />
              </BpkAccordionItem>
              <BpkAccordionItem id="departure-times" title="Payment info (not implemented)">
        Nothing to see here
              </BpkAccordionItem>
            </SingleItemAccordion>
      }
        </main>
      </div>
    );
  }
}

export default App;
